.footer{
	color: white;
	@include col();

	

	
	.footer__top__wrap{
		@include row-flex();
		@include justify-center;
		min-height: 655px;
		height: 100%;
		background: url('../../img/footer-background1.jpg')  no-repeat;
		
		.footer__top{
		@include container;
		@include row-flex();
		@include justify-center;
		width: 100%;
		
	}

		.footer__top__social{
			@include row-flex();
			@include size(6);
			@include space-between;
			@include align-center;
			padding: 140px 20px 0 10px;

			.insta{
				
				font-size: 0;
				display: block;
				width: 100px;
				height: 108px;
				background: url('../../img/footer-top-insta.png') no-repeat;
			}

			.vk{
				font-size: 0;
				display: block;
				width: 100px;
				height: 108px;
				background: url('../../img/footer-top-vk.png') no-repeat 50% 30%;
			}

			.whatsapp{
				font-size: 0;
				display: block;
				width: 100px;
				height: 108px;
				background: url('../../img/footer-top-whatsapp.png') no-repeat 120% 400%;
			}
		}
		a{
			display: block;
			@include size(8);
			font-family: GeometriaRegular;
			font-size: 24px;
		}

	}
	.footer__bottom{
		height: 105px;
		@include row-flex();
		@include space-between;
		background: #2f2f2f;
		@include align-center;
		
		span{
			display: block;
			@include size(4);
			font-family: GeometriaLight;
			font-size: 30px;
		}

		.footer__bottom__social{
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			@include size(4);
			@include justify-center;
			padding-right: 111px;

			.bottom__insta{
				font-size: 0;
				display: block;
				width: 40px;
				height: 40px;
				background: url('../../img/footer-top-insta.png') no-repeat;
				-webkit-background-size: contain;
				background-size: contain;
				margin-right: 55px;
			}

			.bottom__vk{
				font-size: 0;
				display: block;
				width: 40px;
				height: 40px;
				background: url('../../img/footer-top-vk.png') 50% no-repeat;
				-webkit-background-size: contain;
				background-size: contain;	
			}

			.bottom__whatsapp{
				font-size: 0;
				display: block;
				width: 40px;
				height: 40px;
				background: url('../../img/footer-top-whatsapp.png') 50% 250% no-repeat;
				-webkit-background-size: contain;
				background-size: contain;
				margin-left: 60px
			}
		}
	}
		
	
}
