body {
	font-family: "Arial", sans-serif;
	font-size: 16px;
	width: 100%;
	height: 100%;
	color: #35393a;
}

div, a, ul, li {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul {
	padding: 0;
	margin: 0;
	  margin-block-start: 0;
        margin-block-end: 0;
}

li, a{
		text-decoration: none;
		list-style-type: none;
	}

.container {
	@include container();
	@include sm(max-width, 510px);
	@include md(max-width, 690px);
	@include lg(max-width, 930px);
	@include xl(max-width, 1110px);
}

.img-responsive {
	max-width: 800px;
	width: 100%;
	height: auto;
}

#wrapper{
		width: 100%;
		max-width: 1400px;
		margin: auto;
		text-align: center;
	}