@import "../../styles/vendor/_smart-grid.scss";

.header{
	background: url('../../img/top-background.jpg');
	@include container;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	@include justify-center;
	max-width: 1400px;

	.wrap{
		@include container();
		max-width: 1046px;
		width: 100%;
		@include row-flex();
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-webkit-flex-direction: column;
		-ms-flex-direction: column;
		flex-direction: column;
		@include space-between;
			min-height: 772px;


		.header__nav{
			@include row-flex();
			@include space-between;
			margin-top: 6.5%;
			margin-left: 4px;
			margin-right: 0;
			color: white;

			.nav__portfolio{
				margin-top: 7px;
				margin-left: 2px;
			}

			.nav__about{
				margin-top: 5px;
				margin-right: 30px;
			}

			.nav__price{
				margin-top: 8px;
				margin-right: 15px;
			}

			.nav__contact{
				margin-top: 5px;
				margin-right: 20px;
			}

			a {
				color: white;
				font-family: Geometria;
				font-size: 2.375rem;
				font-weight: 400;
			}

			li{
				&:hover{
					display: block;
					border-bottom: 3px solid white;
					padding-top: 3px;
				}
			}

		}

		.header__tagline{
			margin-bottom: 6.5%;
			@include row-flex();
			color: white;
			width: 100%;
			@include size(7);
			text-align: left;
			margin-left: -15px;

			.tagline__hello{
				font-family: GeometriaExtraBold;
				font-size: 55px;	
				margin-bottom: 18px;
				letter-spacing: .189rem;
				font-weight: 400;
			}
			.tagline__soul{
				font-family: GeometriaLight;
				font-size: 36px;	
				@include col();
				@include size(9);
				margin-left: 0;
			}

		}
	}
}
