@import "../../styles/vendor/_smart-grid.scss";

@include reset();

.main__wrap{

	@include container;
	
	font-family: GeometriaRegular;
	
	.portfolio__title-wrap{
		@include row-flex();
		@include justify-center;
		height: 100%;
		padding-top: 7.1%;

		h2{
			font-size: 2.5rem;
			@include col();
			@include size(12);
			padding-left: 5px;

		}
		.portfolio__tags{
			@include row-flex();
			@include size(9);
			@include space-around;
			padding: 50px 2px 55px 20px;

			a{
				font-size: 2.375rem;
				color: black;
			}
		}
	}
	.portfolio__image-wrap{
		@include row-flex();
		width: 100%;
		margin-top: 1.2%;
		margin-left: 0;
		@include justify-center;

		.portfolio__image{

			@include col();
			@include size(4);
			margin: 0 0 4.15% 0;
		}

		.portfolio__more{
			font-size: 24px;

			&:hover{
				border-bottom: 1px dashed black;
				cursor: pointer;
			}
		}

	}
	


	/***************** ABOUT ****************/

	.about__wrap{
		margin-top: 6.2%;
		@include row-flex();
		@include space-between;
		@include align-center;

		.about__img{
			@include col();
			@include size(6);
			height: 100%;
			min-height: 627px;

		}
		.about__text-wrap{
			@include col();
			@include size(5);
			@include align-center;
			text-align: left;



			.about__text{
				margin-bottom: 13%;
			}

			.about__yours{
				font-size: 1.875rem;
				font-family: GeometriaRegular;

			}
			.about__name{
				font-size: 3rem;
				font-family: GeometriaMedium;
				color: #35393a;
				letter-spacing: .3rem;
				margin-top: 9%;
				margin-bottom: 6%;
				
			}
			.about__text-descript{
				letter-spacing: .015rem;
				font-family: GeometriaMedium;
				font-size: 1.25rem;
				color: #787878;
				line-height: 1.5;
			}

			.about__subscribe{
				@include col();
				@include size(12);
				font-family: GeometriaRegular;
				font-size: 1.5rem;
				margin-left: 5%;
				cursor: pointer;

				&:after{
					content: "";
					display: block;
					width: 185px;
					height: 13px;
					background: url('../../img/subscribe-sign.jpg');
					margin-left: 10px;
				}

				.about__subscribe:hover::after{
					display: none;
				}

			}
		}
	}

	/************* FEEDBACK **********/

	.feedback_wrap{
		position: relative;
		margin-top: 11%;
		@include row-flex();
		@include size(12);
		-webkit-box-pack: center;
		-webkit-justify-content: center;
		-ms-flex-pack: center;
		justify-content: center;	
		width: 100%;

		&:before{
			content: "";
			background: url('../../img/feedback-quotes-top.png') no-repeat;
			width: 100%;
			height: 35%;
			position: absolute;
			top: 33%;
			left: 2.5%;
		}

		&:after{
			content: "";
			background: url('../../img/feedback-quotes-bot.png') no-repeat;
			width: 146px;
			height: 263px;
			position: absolute;
			right: 4%;
			bottom:-4%;
		}

		.feedback__title{
			@include col();
			@include size (12);
			font-family: GeometriaRegular;
			font-size: 40px;

		}		
		.slider{
			font-family: GeometriaMedium;
			font-size: 20px;
			@include row-flex;
			@include justify-center;
			margin-top: 75px;

			.slider__items{
				@include col();
				@include size(8);

				.feedback__photo-wrap{
					@include row-flex();
					@include justify-center;

					.feedback__photo-overlay{
						@include col();
						@include size (12);
						z-index: 1000;
						position: relative;
						width: 210px;
						height: 210px;
						background: url('../../img/feedback-overlay.png') no-repeat center;

						.feedback__user-photo{
							position: absolute;
							background: url('../../img/feedback-photo.jpg') no-repeat center;
							background-position: -20px;
							top: 6%;
							left: 5%;
							width: 185px;
							height: 185px;
							-webkit-border-radius: 50%;
							border-radius: 50%;
							
						}
					}
				}
				.item_text{
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-webkit-align-items: center;
					-ms-flex-align: center;
					align-items: center;
					margin-top: 65px;
					line-height: 1.75;
					height: 264px;
				}

			}
		}
	}

	/*********************  PRICES ******************/

	.prices__wrap{
		@include row-flex;
		@include space-between;
		padding: 85px 0 70px 0;

		.prices__item_back_1{
			background: url('../../img/examplePhoto.jpg') no-repeat;
		}

		.prices__item_back_2{
			background: url('../../img/PriceBack2.jpg') 50% 100% no-repeat;
			-webkit-background-size: cover;
			background-size: cover;
		}
		.prices__item_back_3{
			background: url('../../img/PriceBack3.jpg') 40% -7% no-repeat;

		}
		
		.prices__item{
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			@include col();
			@include size(4);
			height: 630px;
			min-height: 100%;
			max-width: 355px;
			color: white;
			-webkit-box-align: end;
			-webkit-align-items: flex-end;
			-ms-flex-align: end;
			align-items: flex-end;
			@include justify-center;
			position: relative;

			&:hover{
				opacity: .9;
				-webkit-background-size: auto auto;
				        background-size: auto 110%;
				        cursor: pointer;
			}
			
			.item-shadow{
				background: #585858;
				position: absolute;
				top: 0;
				left: 0;
				display: block;
				max-width: 355px;
				width: 100%;
				height: 100%;
				max-height: 630px;
				opacity: 0.5;



			}
			.item__wrap{
				display: -webkit-box;
				display: -webkit-flex;
				display: -ms-flexbox;
				display: flex;
				-webkit-flex-wrap: wrap;
				-ms-flex-wrap: wrap;
				flex-wrap: wrap;
				@include justify-center;
				height: 100%;
				z-index: 100;
				position: relative;

				.item__name{
					font-family: GeometriaExtraBold;
					font-size: 60px;
					letter-spacing: 3px;
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-webkit-align-items: center;
					-ms-flex-align: center;
					align-items: center;
					height: 100%;
				}

				.price__wrap{
					height: 38%;
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
					-webkit-flex-wrap: wrap;
					-ms-flex-wrap: wrap;
					flex-wrap: wrap;
					@include justify-center;
					position: absolute;
					bottom: 5%;
					-webkit-align-content: space-between;
					-ms-flex-line-pack: justify;
					align-content: space-between;
					height: 30%;
					z-index: 100;
					right: 0;
					
					font-weight: 400;



					.item__price{
						width: 100%;
						font-size: 36px;
						letter-spacing: 2px;
					}

					.item__more{
						font-family: GeometriaRegular;
						font-size: 30px;
						letter-spacing: 2px;
						color: white;
					}
				}
			}
		}

	}

	/*************** INSTAGRAM ************/

	.insta__wrap{
		@include col();
		@include size(12);
		font-family: GeometriaRegular;

		h2{
			font-size: 40px;
		}

		.insta__photo__wrap{
			@include row-flex;
			@include space-between;
			padding-top: 55px;

		}

		a{
			color: #35393a;
			display: block;
			padding-top: 80px;
			padding-bottom: 80px;
			font-size: 24px;
		}
	}
}



